/* Загальні стилі */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    line-height: 1.6;
    color: #333;
    background-color: #f9f9f9;
  }
  
  h1, h2, h3 {
    margin: 0;
  }
  
  a {
    color: #007acc;
    text-decoration: none;
    transition: color 0.3s;
  }
  
  a:hover {
    color: #005b99;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  button {
    background-color: #007acc;
    color: #fff;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #005b99;
  }
  
  /* Header */
  header {
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
  }
  
  .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .navigation {
    display: flex;
    gap: 20px;
  }
  
  .navigation li {
    font-size: 14px;
  }
  
  .pop-up {
    font-size: 14px;
  }
  
  /* Головний слайд */
  .main-slide {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 20px;
    background-color: #fff;
  }
  
  .info {
    flex: 1;
    text-align: center;
  }
  
  .info img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    margin-top: 15px;
  }
  
  /* Секція "Про нас" */
  #about {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #f4f4f4;
    padding: 20px;
  }
  
  #about h3 {
    flex: 1;
    text-align: center;
    font-size: 16px;
  }
  
  /* Секція історії виставок */
  .exh-history {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
    padding: 20px;
    background-color: #fff;
  }
  
  .exh-history div {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 15px;
    text-align: center;
    transition: transform 0.3s;
  }
  
  .exh-history div:hover {
    transform: translateY(-5px);
  }
  
  .exh-history img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  .exh-link, .history-link {
    text-decoration: none;
    color: #333;
  }
  
  /* Footer */
  footer {
    background-color: #333;
    color: #fff;
    text-align: center;
    padding: 20px;
  }
  
  footer h2 {
    margin-bottom: 10px;
  }
  
  footer p {
    margin: 5px 0;
    font-size: 14px;
  }
  
  footer form {
    margin: 15px 0;
  }
  
  footer input[type="email"] {
    padding: 10px;
    border: none;
    border-radius: 5px;
    margin-right: 10px;
    width: calc(100% - 130px);
    max-width: 300px;
  }
  
  .links {
    display: flex;
    justify-content: center;
    gap: 15px;
    padding: 10px 0;
  }
  
  .links a {
    color: #fff;
    font-size: 14px;
  }
  
  .links a:hover {
    text-decoration: underline;
  }
  
  /* Адаптивність */
  @media (max-width: 768px) {
    .menu {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .navigation {
      flex-direction: column;
      gap: 10px;
    }
  
    .main-slide {
      flex-direction: column;
    }
  
    #about {
      flex-direction: column;
      text-align: center;
    }
  
    .exh-history {
      grid-template-columns: 1fr;
    }
  
    footer input[type="email"] {
      width: calc(100% - 100px);
    }
  }
  